.modal-footer {
  width: 280px;
  height: 380px;
  border-radius: 5px;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  background-color: get(bg-secondary);
  color: get(text-secondary);
  text-align: center;
  font-family: 'Roboto';
  font-size: 14px;
  &__icon-close,
  &__icon-left,
  &__icon-right {
    position: fixed;
    width: 22px;
    height: 22px;
    transform: translateY(-50%);
    fill: get(text-secondary);
    transition-property: fill;
    transition-duration: 250ms;
    &:hover,
    &:focus {
      cursor: pointer;
      fill: get(bg-decorative);
    }
  }
  &__icon-close {
    top: 20px;
    right: 10px;
  }
  &__icon-left {
    top: 50%;
    left: 10px;
  }
  &__icon-right {
    top: 50%;
    right: 10px;
  }
  &__icon-hidden {
    visibility: hidden;
  }
  &__students-list {
    padding: 0;
  }
  &__student {
    display: none;
  }
  &__student-active {
    display: unset;
  }
  &__student-image {
    width: 200px;
    padding-top: 50px;
  }
  &__student-text {
    display: none;
  }
}
.modal-footer-backdrop {
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  z-index: 1;
  top: 0px;
  background-color: rgba(0, 0, 0, 0.7);
  transition: opacity, visibility;
  transition-duration: 250ms;
  transition-timing-function: linear;
}
.is-hidden {
  visibility: hidden;
  pointer-events: none;
  opacity: 0;
}
@media screen and (min-width: 768px) {
  .modal-footer {
    width: 650px;
    height: 450px;
    font-size: 18px;
    &__student-data {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 30px;
    }
    &__student-text {
      display: unset;
      width: 300px;
      padding-top: 20px;
      text-align: justify;
    }
  }
}
